<template>
  <div class="mdl-grid">
    <div class="mdl-cell mdl-cell--8-col">
      <div class="picture">
        <img :src="this.manga.url" />
      </div>
      <div class="content" v-html="this.manga.content">
        {{ this.manga.content }}
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      'manga': []
    }
  },
  mounted () {
    var contextHolder = this
    var getManga = function (key) {
      var statement = key + '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI'
      var url = 'https://www.googleapis.com/blogger/v3/blogs/8614744959548570909/posts/' + statement
      // Fetch the latest data.
      var request = new XMLHttpRequest()
      request.onreadystatechange = function () {
        if (request.readyState === XMLHttpRequest.DONE) {
          if (request.status === 200) {
            var response = JSON.parse(request.response)
            console.log(response)
            contextHolder.manga = response
          }
        }
      }
      request.open('GET', url)
      request.send()
    }
    getManga(this.$route.params.id)
  }
}
</script>
<style scoped>
  .info {
    text-align: right;
    padding: 5px;
    color: #555;
    font-size: 10px;
  }
</style>


