<template>
  <div>
    <div class="mdl-grid">
      <div class="mdl-cell mdl-cell--3-col mdl-cell mdl-cell--1-col-tablet mdl-cell--hide-phone"></div>
      <div class="mdl-cell mdl-cell--6-col mdl-cell--4-col-phone">
        <div v-for="picture in this.pictures" class="image-card" @click="displayDetails(picture.id)">
          <div class="image-card__picture">
            <img :src="picture.image" />
          </div>
          <div class="image-card__comment mdl-card__actions">
            <span>{{ picture.title }}</span>
          </div>
        </div>
      </div>
    </div>
    <router-link class="add-picture-button mdl-button mdl-js-button mdl-button--fab mdl-button--colored" to="/post">
      <i class="material-icons">add</i>
    </router-link>
  </div>
</template>
<script>
  export default {
    methods: {
      displayDetails (id) {
        this.$router.push({name: 'detail', params: { id: id }})
      }
    },
    data () {
      return {
        'pictures': []
      }
    },
    mounted () {
      var contextHolder = this
      var getList = function (paginate) {
        console.log('[pagination]' + paginate == null)
        var statement = ''
        if (paginate == null) {
          statement = '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI&fetchBodies=false&status=live&fetchImages=true'
        } else {
          statement = '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI&fetchBodies=false&status=live&fetchImages=true&pageToken=' + paginate
        }
        var url = 'https://www.googleapis.com/blogger/v3/blogs/8614744959548570909/posts' + statement
        // Fetch the latest data.
        var request = new XMLHttpRequest()
        request.onreadystatechange = function () {
          if (request.readyState === XMLHttpRequest.DONE) {
            if (request.status === 200) {
              var response = JSON.parse(request.response)
              console.log(response)
              response.items.forEach(function (postAbstract) {
                if (postAbstract.images[0] != null) {
                  postAbstract.image = postAbstract.images[0].url.replace('/s1600/', '/s400/') // downsample image size
                }
                contextHolder.pictures.push(postAbstract)
              })
              if (response.nextPageToken != null) {
                getList(response.nextPageToken)
              }
            }
          }
        }
        request.open('GET', url)
        request.send()
      }
      getList()
    }
  }
</script>
<style scoped>
  .add-picture-button {
    position: fixed;
    right: 24px;
    bottom: 24px;
    z-index: 998;
  }
  .image-card {
    position: relative;
    margin-bottom: 8px;
  }
  .image-card__picture > img {
    width:100%;
  }
  .image-card__comment {
    position: absolute;
    bottom: 0;
    height: 52px;
    padding: 16px;
    text-align: right;
    background: rgba(0, 0, 0, 0.5);
  }
  .image-card__comment > span {
    color: #fff;
    font-size: 14px;
    font-weight: bold;
  }
</style>


